.post {
  width: 100%;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin: 30px 0;
}

.postWrapper {
  padding: 10px;
}

.postTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.postTopLeft {
  display: flex;
  align-items: center;
}
.postProfileImg {
  width: 32px;
  height: 32px;
  object-fit: cover;
  border-radius: 50%;
}

.postUsername{
    font-size: 15px;
    font-weight: 500;
    margin : 0 10px;
}


.postDate{
    font-size: 12px;
}

.postCenter{
    margin: 20px 0;
}

.postImg{
    width: 100%;
     max-width: 500px;
     object-fit: contain;
     margin-top: 20px;
}

.postBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.postBottomLeft{
    display: flex;
    align-items: center;
}

.likeIcon{
    width: 24px;
    height: 24px;
    margin-right: 5px;
    cursor: pointer;
}


.postLikeCounter{
    font-size: 15px;
}

.postBottomRight{
    cursor: pointer;
    border-bottom: 1px dashed gray;
    font-size: 15px;
}